export default {
	data() {
		return {
			taskId: ''
		}
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 班级成员跳转
		linkTo(studentId) {
			uni.navigateTo({
				url: 'studentDetails?studentId=' + studentId
			})
		}

	},
	created() {

	},

	onLoad(options) {
		// var data = JSON.parse(options.index); // 字符串转对象
		console.log(options)
		this.taskId = options.taskId
	}
}
